<template>
  <div class="container">
    <div class="left">
      <card v-for="gallup in gallupList" :gallup="gallup" @choose="choose"></card>
    </div>
    <!-- <div class="right">
      <div v-for="a in selected">{{a.name}}</div>
    </div>-->
    <!-- <div class="re-box">
      <div class="re-item">
        <dl class="re-item-front">
          <dt>
            <img src="img/c2.jpg" alt="coser" />
          </dt>
          <dd class="re-item-job">coser</dd>
        </dl>

        <div class="re-item-back">
          <img src="img/c2_1.jpg" alt="苏苏" />
          <button type="button" class="join_btn open_modal" data-id="2">立即加入</button>
        </div>
      </div>
    </div>-->
    <div class="selected-list">
      TOP10优势排序
      <div
        v-for="item, index in selected"
        draggable="true"
        class="items"
        @dragstart="dragstart(item)"
        @dragenter="dragenter(item)"
        @dragend="dragend(item)"
        @touchstart="dragstart(item)"
        @touchmove="dragenter(item)"
        @touchend="dragend(item)"
      >
        {{index + 1}}
        {{item.name}}
      </div>
    </div>
  </div>
</template>

<script>
import gallup from "../db/gallup.js";
import Card from "./Card";
import draggable from "vuedraggable";

export default {
  name: "HelloWorld",
  components: {
    Card,
    draggable,
  },
  data() {
    return {
      gallupList: [],
      selected: [],
      oldNum: 0,
      newNum: 0,
      count: 0,
    };
  },
  mounted() {
    this.gallupList = gallup.gallup;
  },
  methods: {
    choose(id) {
      if (this.gallupList[id - 1].selected) {
        this.count--;
        // 删除元素
        let exists = this.selected.indexOf(this.gallupList[id - 1]);
        this.selected.splice(exists, 1);
        this.gallupList[id - 1].selected = false;
        this.gallupList[id - 1].selectedCount = this.count;
      } else {
        if (this.count >= 10) {
          alert("最多10个，您可以再次选择来删除");
          return;
        }
        this.gallupList[id - 1].selected = true;
        this.count++;
        this.gallupList[id - 1].selectedCount = this.count;
        this.selected.push(this.gallupList[id - 1]);
      }
    },
    submit() {
      console.log(this.selected, "....");
    },
    dragstart: function (value) {
      this.oldNum = value;
    },
    // 做最终操作
    dragend: function (value) {
      if (this.oldNum != this.newNum) {
        let oldIndex = this.selected.indexOf(this.oldNum);
        let newIndex = this.selected.indexOf(this.newNum);
        let newItems = [...this.selected];
        // 删除老的节点
        newItems.splice(oldIndex, 1);
        // 在列表中目标位置增加新的节点
        newItems.splice(newIndex, 0, this.oldNum);
        // this.items一改变，transition-group就起了作用
        this.selected = [...newItems];
      }
    },
    // 记录移动过程中信息
    dragenter: function (value) {
      this.newNum = value;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.left {
  width: 80%;
  float: left;
  background-color: yellow;
}

/* .right {
  width: 400px;
  height: 100px;
  background-color: burlywood;
  float: right;
} */
.flip-list-move {
  transition: transform 1s;
}
.items {
  width: 75px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border: 1px solid red;
  display: inline-block;
}
.selected-list {
  width: 80px;
  position: fixed;
  right: 1px;
  top: 5px;
}
.re-item {
  width: 220px;
  margin: 50px auto;
  height: 276px;
  cursor: pointer;
  position: relative;
  perspective: 500;
  -webkit-perspective: 500;
}

.re-item img {
  max-width: 220px;
}

.re-item-front,
.re-item-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-perspective: 1000;
  /*backface-visibility: hidden;*/
  -webkit-transition: all 1.5s;
  -moz-transition: all 1.5s;
  -ms-transition: all 1.5s;
  -o-transition: all 1.5s;
  box-shadow: rgba(50, 50, 50, 0.2) 0 0 15px;
  overflow: hidden;
}

.re-item-back {
  position: relative;
  transform: rotateY(-180deg);
  -webkit-transform: rotateY(-180deg);
}

.re-item:hover .re-item-front {
  transform: rotateY(-180deg);
  -webkit-transform: rotateY(-180deg);
}

.re-item:hover .re-item-back {
  transform: rotateY(-360deg);
  -webkit-transform: rotateY(-360deg);
}

.re-box .re-item dd {
  text-align: center;
  font-family: "微软雅黑";
}

.re-item .re-item-job {
  margin: 2px 0;
  font-size: 18px;
  color: #303030;
  line-height: 40px;
}

.re-item {
  padding: 0 12px;
  font-size: 14px;
  color: #939393;
}
</style>



